.page {
  height: 100%;
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 134.4%; }

.page__body {
  margin: 0;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: grid;
  grid-template-rows: -webkit-max-content 1fr -webkit-max-content;
  grid-template-rows: max-content 1fr max-content;
  background-color: #ffffff;
  color: #666666;
  position: relative; }

.page-header__navigation {
  max-width: 1420px;
  min-height: 52px;
  margin: 0 auto;
  padding: 0 20px;
  background-color: #1b2d37; }

.site-navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.site-navigation__logo {
  height: 52px;
